'use client';

import React, { useState } from 'react';
import { useGame } from '@/context/GameContext';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';

const ItemCreator: React.FC = () => {
  const { character, createCustomItem } = useGame();
  
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    type: 'misc' as 'weapon' | 'armor' | 'consumable' | 'misc',
    rarity: 'common' as 'common' | 'uncommon' | 'rare' | 'epic' | 'legendary',
    value: 10,
    damage: 0,
    armor: 0,
    healing: 0
  });

  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateForm = () => {
    const newErrors: Record<string, string> = {};
    
    if (!formData.name.trim()) {
      newErrors.name = 'Item name is required';
    }
    
    if (!formData.description.trim()) {
      newErrors.description = 'Item description is required';
    }
    
    if (formData.value < 1) {
      newErrors.value = 'Item value must be at least 1';
    }
    
    if (formData.type === 'weapon' && formData.damage < 1) {
      newErrors.damage = 'Weapons must have damage value';
    }
    
    if (formData.type === 'armor' && formData.armor < 1) {
      newErrors.armor = 'Armor must have armor value';
    }
    
    if (formData.type === 'consumable' && formData.healing < 1) {
      newErrors.healing = 'Consumables must have healing value';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!character) {
      setErrors({ general: 'Please create a character first' });
      return;
    }
    
    if (!validateForm()) {
      return;
    }
    
    try {
      const stats: any = {};
      
      if (formData.type === 'weapon' && formData.damage > 0) {
        stats.damage = formData.damage;
      }
      
      if (formData.type === 'armor' && formData.armor > 0) {
        stats.armor = formData.armor;
      }
      
      if (formData.type === 'consumable' && formData.healing > 0) {
        stats.healing = formData.healing;
      }
      
      createCustomItem({
        name: formData.name.trim(),
        description: formData.description.trim(),
        type: formData.type,
        rarity: formData.rarity,
        value: formData.value,
        stats: Object.keys(stats).length > 0 ? stats : undefined
      });
      
      // Reset form
      setFormData({
        name: '',
        description: '',
        type: 'misc',
        rarity: 'common',
        value: 10,
        damage: 0,
        armor: 0,
        healing: 0
      });
      
      setErrors({});
      
    } catch (error) {
      setErrors({ general: 'Failed to create item. Please try again.' });
    }
  };

  const handleInputChange = (field: string, value: any) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    
    // Clear error for this field
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  if (!character) {
    return (
      <Card className="w-full">
        <CardHeader>
          <CardTitle>Item Creator</CardTitle>
          <CardDescription>Please create a character first to create items</CardDescription>
        </CardHeader>
      </Card>
    );
  }

  return (
    <Card className="w-full">
      <CardHeader>
        <CardTitle>Item Creator</CardTitle>
        <CardDescription>Create custom items for your inventory</CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          {errors.general && (
            <div className="p-3 bg-destructive/10 border border-destructive/20 rounded-md">
              <p className="text-sm text-destructive">{errors.general}</p>
            </div>
          )}
          
          {/* Item Name */}
          <div className="space-y-2">
            <Label htmlFor="item-name">Item Name</Label>
            <Input
              id="item-name"
              value={formData.name}
              onChange={(e) => handleInputChange('name', e.target.value)}
              placeholder="Enter item name..."
              className={errors.name ? 'border-destructive' : ''}
            />
            {errors.name && (
              <p className="text-sm text-destructive">{errors.name}</p>
            )}
          </div>

          {/* Item Description */}
          <div className="space-y-2">
            <Label htmlFor="item-description">Description</Label>
            <Textarea
              id="item-description"
              value={formData.description}
              onChange={(e) => handleInputChange('description', e.target.value)}
              placeholder="Describe the item..."
              rows={3}
              className={errors.description ? 'border-destructive' : ''}
            />
            {errors.description && (
              <p className="text-sm text-destructive">{errors.description}</p>
            )}
          </div>

          {/* Item Type and Rarity */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="item-type">Item Type</Label>
              <Select 
                value={formData.type} 
                onValueChange={(value: any) => handleInputChange('type', value)}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Select item type" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="weapon">Weapon</SelectItem>
                  <SelectItem value="armor">Armor</SelectItem>
                  <SelectItem value="consumable">Consumable</SelectItem>
                  <SelectItem value="misc">Miscellaneous</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="item-rarity">Rarity</Label>
              <Select 
                value={formData.rarity} 
                onValueChange={(value: any) => handleInputChange('rarity', value)}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Select rarity" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="common">Common</SelectItem>
                  <SelectItem value="uncommon">Uncommon</SelectItem>
                  <SelectItem value="rare">Rare</SelectItem>
                  <SelectItem value="epic">Epic</SelectItem>
                  <SelectItem value="legendary">Legendary</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Item Value */}
          <div className="space-y-2">
            <Label htmlFor="item-value">Value (Gold)</Label>
            <Input
              id="item-value"
              type="number"
              min="1"
              value={formData.value}
              onChange={(e) => handleInputChange('value', parseInt(e.target.value) || 0)}
              className={errors.value ? 'border-destructive' : ''}
            />
            {errors.value && (
              <p className="text-sm text-destructive">{errors.value}</p>
            )}
          </div>

          {/* Type-specific Stats */}
          {formData.type === 'weapon' && (
            <div className="space-y-2">
              <Label htmlFor="item-damage">Damage</Label>
              <Input
                id="item-damage"
                type="number"
                min="1"
                value={formData.damage}
                onChange={(e) => handleInputChange('damage', parseInt(e.target.value) || 0)}
                className={errors.damage ? 'border-destructive' : ''}
              />
              {errors.damage && (
                <p className="text-sm text-destructive">{errors.damage}</p>
              )}
            </div>
          )}

          {formData.type === 'armor' && (
            <div className="space-y-2">
              <Label htmlFor="item-armor">Armor Value</Label>
              <Input
                id="item-armor"
                type="number"
                min="1"
                value={formData.armor}
                onChange={(e) => handleInputChange('armor', parseInt(e.target.value) || 0)}
                className={errors.armor ? 'border-destructive' : ''}
              />
              {errors.armor && (
                <p className="text-sm text-destructive">{errors.armor}</p>
              )}
            </div>
          )}

          {formData.type === 'consumable' && (
            <div className="space-y-2">
              <Label htmlFor="item-healing">Healing Amount</Label>
              <Input
                id="item-healing"
                type="number"
                min="1"
                value={formData.healing}
                onChange={(e) => handleInputChange('healing', parseInt(e.target.value) || 0)}
                className={errors.healing ? 'border-destructive' : ''}
              />
              {errors.healing && (
                <p className="text-sm text-destructive">{errors.healing}</p>
              )}
            </div>
          )}

          {/* Preview */}
          {formData.name && (
            <div className="p-4 bg-muted rounded-lg">
              <h4 className="font-medium mb-2">Item Preview</h4>
              <div className="space-y-2">
                <div className="flex items-center gap-2">
                  <span className="font-medium">{formData.name}</span>
                  <Badge variant="outline" className="text-xs">
                    {formData.rarity}
                  </Badge>
                  <Badge variant="secondary" className="text-xs">
                    {formData.type}
                  </Badge>
                </div>
                {formData.description && (
                  <p className="text-sm text-muted-foreground">{formData.description}</p>
                )}
                <div className="flex justify-between items-center text-sm">
                  <div className="flex gap-2">
                    {formData.type === 'weapon' && formData.damage > 0 && (
                      <span>Damage: {formData.damage}</span>
                    )}
                    {formData.type === 'armor' && formData.armor > 0 && (
                      <span>Armor: {formData.armor}</span>
                    )}
                    {formData.type === 'consumable' && formData.healing > 0 && (
                      <span>Healing: {formData.healing}</span>
                    )}
                  </div>
                  <span className="font-medium">{formData.value}g</span>
                </div>
              </div>
            </div>
          )}

          <Button type="submit" className="w-full">
            Create Item
          </Button>
        </form>
      </CardContent>
    </Card>
  );
};

export default ItemCreator;
