'use client';

import React, { useEffect } from 'react';
import { useGame } from '@/context/GameContext';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Progress } from '@/components/ui/progress';
import { Separator } from '@/components/ui/separator';
import { ScrollArea } from '@/components/ui/scroll-area';

const GameDashboard: React.FC = () => {
  const { 
    character, 
    battleState, 
    startBattle, 
    executePlayerAction, 
    executeEnemyTurn, 
    endBattle,
    availableMissions,
    completeMission
  } = useGame();

  // Auto-execute enemy turn after a delay
  useEffect(() => {
    if (battleState.isActive && battleState.currentTurn === 'enemy') {
      const timer = setTimeout(() => {
        executeEnemyTurn();
      }, 1500);
      return () => clearTimeout(timer);
    }
  }, [battleState.currentTurn, battleState.isActive, executeEnemyTurn]);

  if (!character) {
    return (
      <Card className="w-full">
        <CardHeader>
          <CardTitle>Game Dashboard</CardTitle>
          <CardDescription>Please create a character first to access the dashboard</CardDescription>
        </CardHeader>
      </Card>
    );
  }

  const expToNextLevel = character.level * 100;
  const expProgress = (character.experience / expToNextLevel) * 100;

  const handlePlayerAction = (actionType: 'attack' | 'defend' | 'item' | 'run') => {
    if (actionType === 'item') {
      // Use first health potion if available
      const healthPotion = character.inventory.find(item => item.type === 'consumable' && item.stats?.healing);
      if (healthPotion) {
        executePlayerAction({ type: 'item', itemId: healthPotion.id });
      }
    } else {
      executePlayerAction({ type: actionType });
    }
  };

  const handleCompleteMission = (missionId: string) => {
    completeMission(missionId);
  };

  return (
    <div className="w-full space-y-6">
      {/* Character Overview */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center justify-between">
            Character Overview
            <Badge variant="outline">Level {character.level}</Badge>
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {/* Basic Info */}
            <div className="space-y-3">
              <div>
                <h4 className="font-semibold">{character.name}</h4>
                <p className="text-sm text-muted-foreground">
                  {character.race.name} {character.mainClass?.name || 'Classless'}
                  {character.secondaryClass && ` / ${character.secondaryClass.name}`}
                </p>
              </div>
              
              <div>
                <div className="flex justify-between text-sm mb-1">
                  <span>Experience</span>
                  <span>{character.experience} / {expToNextLevel}</span>
                </div>
                <Progress value={expProgress} className="h-2" />
              </div>
              
              <div className="flex justify-between text-sm">
                <span>Gold:</span>
                <span className="font-medium">{character.gold}</span>
              </div>
            </div>

            {/* Health & Mana */}
            <div className="space-y-3">
              <div>
                <div className="flex justify-between text-sm mb-1">
                  <span>Health</span>
                  <span>{character.stats.hp} / {character.stats.maxHp}</span>
                </div>
                <Progress 
                  value={(character.stats.hp / character.stats.maxHp) * 100} 
                  className="h-2"
                />
              </div>
              
              <div>
                <div className="flex justify-between text-sm mb-1">
                  <span>Mana</span>
                  <span>{character.stats.mp} / {character.stats.maxMp}</span>
                </div>
                <Progress 
                  value={(character.stats.mp / character.stats.maxMp) * 100} 
                  className="h-2"
                />
              </div>
            </div>

            {/* Stats */}
            <div className="space-y-2">
              <h4 className="font-semibold text-sm">Ability Scores</h4>
              <div className="grid grid-cols-2 gap-2 text-xs">
                <div className="flex justify-between">
                  <span>STR:</span>
                  <span className="font-medium">{character.stats.strength}</span>
                </div>
                <div className="flex justify-between">
                  <span>DEX:</span>
                  <span className="font-medium">{character.stats.dexterity}</span>
                </div>
                <div className="flex justify-between">
                  <span>CON:</span>
                  <span className="font-medium">{character.stats.constitution}</span>
                </div>
                <div className="flex justify-between">
                  <span>INT:</span>
                  <span className="font-medium">{character.stats.intelligence}</span>
                </div>
                <div className="flex justify-between">
                  <span>WIS:</span>
                  <span className="font-medium">{character.stats.wisdom}</span>
                </div>
                <div className="flex justify-between">
                  <span>CHA:</span>
                  <span className="font-medium">{character.stats.charisma}</span>
                </div>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Battle System */}
      <Card>
        <CardHeader>
          <CardTitle>Battle System</CardTitle>
          <CardDescription>
            {battleState.isActive 
              ? `Turn ${battleState.turnCount} - ${battleState.currentTurn === 'player' ? 'Your Turn' : 'Enemy Turn'}`
              : 'Start a battle to test your combat skills'
            }
          </CardDescription>
        </CardHeader>
        <CardContent>
          {!battleState.isActive ? (
            <div className="text-center">
              <Button onClick={startBattle} size="lg">
                Start Battle
              </Button>
            </div>
          ) : (
            <div className="space-y-4">
              {/* Enemy Status */}
              {battleState.enemy && (
                <div className="p-4 bg-muted rounded-lg">
                  <div className="flex justify-between items-center mb-2">
                    <h4 className="font-semibold">{battleState.enemy.name}</h4>
                    <Badge variant="outline">Level {battleState.enemy.level}</Badge>
                  </div>
                  <div>
                    <div className="flex justify-between text-sm mb-1">
                      <span>HP</span>
                      <span>{battleState.enemy.hp} / {battleState.enemy.maxHp}</span>
                    </div>
                    <Progress 
                      value={(battleState.enemy.hp / battleState.enemy.maxHp) * 100} 
                      className="h-2"
                    />
                  </div>
                </div>
              )}

              {/* Battle Actions */}
              {battleState.currentTurn === 'player' && (
                <div className="flex flex-wrap gap-2">
                  <Button onClick={() => handlePlayerAction('attack')}>
                    Attack
                  </Button>
                  <Button onClick={() => handlePlayerAction('defend')} variant="secondary">
                    Defend
                  </Button>
                  <Button 
                    onClick={() => handlePlayerAction('item')} 
                    variant="secondary"
                    disabled={!character.inventory.some(item => item.type === 'consumable')}
                  >
                    Use Item
                  </Button>
                  <Button onClick={() => handlePlayerAction('run')} variant="outline">
                    Run Away
                  </Button>
                </div>
              )}

              {/* Battle Log */}
              <div>
                <h4 className="font-semibold mb-2">Battle Log</h4>
                <ScrollArea className="h-32 w-full border rounded-md p-2">
                  <div className="space-y-1">
                    {battleState.battleLog.map((log, index) => (
                      <p key={index} className="text-sm">
                        {log}
                      </p>
                    ))}
                  </div>
                </ScrollArea>
              </div>

              {/* End Battle */}
              {!battleState.isActive && (
                <Button onClick={endBattle} variant="outline" className="w-full">
                  Close Battle
                </Button>
              )}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Inventory */}
      <Card>
        <CardHeader>
          <CardTitle>Inventory</CardTitle>
          <CardDescription>Your items and equipment</CardDescription>
        </CardHeader>
        <CardContent>
          {character.inventory.length === 0 ? (
            <p className="text-muted-foreground text-center py-4">No items in inventory</p>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-3">
              {character.inventory.map((item, index) => (
                <div key={index} className="p-3 border rounded-lg">
                  <div className="flex justify-between items-start mb-2">
                    <h5 className="font-medium">{item.name}</h5>
                    <Badge variant="outline" className="text-xs">
                      {item.rarity}
                    </Badge>
                  </div>
                  <p className="text-xs text-muted-foreground mb-2">{item.description}</p>
                  <div className="flex justify-between items-center">
                    <Badge variant="secondary" className="text-xs">
                      {item.type}
                    </Badge>
                    <span className="text-xs font-medium">{item.value}g</span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Available Missions */}
      <Card>
        <CardHeader>
          <CardTitle>Available Missions</CardTitle>
          <CardDescription>Complete missions to gain experience and unlock new classes</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
            {availableMissions.filter(m => !m.completed).map((mission) => (
              <div key={mission.id} className="p-4 border rounded-lg">
                <div className="flex justify-between items-start mb-2">
                  <div>
                    <h5 className="font-medium">{mission.name}</h5>
                    <p className="text-sm text-muted-foreground">{mission.location}</p>
                  </div>
                  <Badge variant={
                    mission.difficulty === 'easy' ? 'secondary' :
                    mission.difficulty === 'medium' ? 'default' :
                    mission.difficulty === 'hard' ? 'destructive' : 'outline'
                  }>
                    {mission.difficulty}
                  </Badge>
                </div>
                <p className="text-sm mb-3">{mission.description}</p>
                <div className="flex justify-between items-center">
                  <div className="text-xs text-muted-foreground">
                    Rewards: {mission.rewards.experience} XP, {mission.rewards.gold} Gold
                    {mission.rewards.unlocksClass && `, Unlocks ${mission.rewards.unlocksClass}`}
                  </div>
                  <Button 
                    size="sm" 
                    onClick={() => handleCompleteMission(mission.id)}
                  >
                    Complete
                  </Button>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default GameDashboard;
