// Pokemon-style Battle Engine for Turn-based Combat

import { Character, BattleState } from './gameData';

export interface BattleAction {
  type: 'attack' | 'defend' | 'item' | 'run';
  target?: 'self' | 'enemy';
  itemId?: string;
}

export interface Enemy {
  name: string;
  level: number;
  hp: number;
  maxHp: number;
  attack: number;
  defense: number;
  speed: number;
  moves: BattleMove[];
}

export interface BattleMove {
  name: string;
  power: number;
  accuracy: number;
  type: 'physical' | 'magical' | 'status';
  description: string;
}

// Sample enemy moves
const ENEMY_MOVES: BattleMove[] = [
  {
    name: 'Slash',
    power: 40,
    accuracy: 95,
    type: 'physical',
    description: 'A basic slashing attack'
  },
  {
    name: 'Bite',
    power: 35,
    accuracy: 100,
    type: 'physical',
    description: 'A vicious bite attack'
  },
  {
    name: 'Magic Missile',
    power: 45,
    accuracy: 100,
    type: 'magical',
    description: 'An unerring magical projectile'
  },
  {
    name: 'Heal',
    power: 30,
    accuracy: 100,
    type: 'status',
    description: 'Restores HP'
  }
];

// Sample enemies
export const SAMPLE_ENEMIES: Enemy[] = [
  {
    name: 'Goblin Warrior',
    level: 1,
    hp: 25,
    maxHp: 25,
    attack: 8,
    defense: 3,
    speed: 12,
    moves: [ENEMY_MOVES[0], ENEMY_MOVES[1]]
  },
  {
    name: 'Orc Berserker',
    level: 3,
    hp: 45,
    maxHp: 45,
    attack: 12,
    defense: 5,
    speed: 8,
    moves: [ENEMY_MOVES[0], ENEMY_MOVES[1]]
  },
  {
    name: 'Dark Mage',
    level: 4,
    hp: 35,
    maxHp: 35,
    attack: 15,
    defense: 4,
    speed: 10,
    moves: [ENEMY_MOVES[2], ENEMY_MOVES[3]]
  },
  {
    name: 'Ancient Dragon',
    level: 10,
    hp: 120,
    maxHp: 120,
    attack: 25,
    defense: 15,
    speed: 15,
    moves: [ENEMY_MOVES[0], ENEMY_MOVES[2]]
  }
];

export class BattleEngine {
  private battleState: BattleState;
  private character: Character;
  private enemy: Enemy | null = null;

  constructor(character: Character) {
    this.character = character;
    this.battleState = {
      isActive: false,
      currentTurn: 'player',
      turnCount: 0,
      battleLog: [],
      enemy: null
    };
  }

  // Initialize battle with random enemy
  initializeBattle(): BattleState {
    const randomEnemy = SAMPLE_ENEMIES[Math.floor(Math.random() * SAMPLE_ENEMIES.length)];
    this.enemy = { ...randomEnemy }; // Create a copy
    
    this.battleState = {
      isActive: true,
      currentTurn: this.character.stats.dexterity >= this.enemy.speed ? 'player' : 'enemy',
      turnCount: 1,
      battleLog: [`A wild ${this.enemy.name} appears!`],
      enemy: {
        name: this.enemy.name,
        hp: this.enemy.hp,
        maxHp: this.enemy.maxHp,
        level: this.enemy.level
      }
    };

    return this.battleState;
  }

  // Execute player action
  executePlayerAction(action: BattleAction): BattleState {
    if (!this.battleState.isActive || this.battleState.currentTurn !== 'player' || !this.enemy) {
      return this.battleState;
    }

    let logMessage = '';

    switch (action.type) {
      case 'attack':
        logMessage = this.executeAttack(this.character, this.enemy);
        break;
      case 'defend':
        logMessage = `${this.character.name} takes a defensive stance!`;
        // Reduce incoming damage by 50% next turn (could be implemented with status effects)
        break;
      case 'item':
        logMessage = this.useItem(action.itemId);
        break;
      case 'run':
        if (Math.random() < 0.8) { // 80% chance to run successfully
          logMessage = `${this.character.name} successfully ran away!`;
          this.battleState.isActive = false;
        } else {
          logMessage = `${this.character.name} couldn't escape!`;
        }
        break;
    }

    this.battleState.battleLog.push(logMessage);

    // Check if enemy is defeated
    if (this.enemy.hp <= 0) {
      this.battleState.battleLog.push(`${this.enemy.name} is defeated!`);
      this.battleState.isActive = false;
      this.giveRewards();
      return this.battleState;
    }

    // Switch to enemy turn if battle is still active
    if (this.battleState.isActive) {
      this.battleState.currentTurn = 'enemy';
    }

    return this.battleState;
  }

  // Execute enemy turn
  executeEnemyTurn(): BattleState {
    if (!this.battleState.isActive || this.battleState.currentTurn !== 'enemy' || !this.enemy) {
      return this.battleState;
    }

    // Simple AI: randomly choose a move
    const randomMove = this.enemy.moves[Math.floor(Math.random() * this.enemy.moves.length)];
    let logMessage = '';

    if (randomMove.type === 'status' && randomMove.name === 'Heal') {
      const healAmount = Math.min(randomMove.power, this.enemy.maxHp - this.enemy.hp);
      this.enemy.hp += healAmount;
      logMessage = `${this.enemy.name} uses ${randomMove.name} and recovers ${healAmount} HP!`;
    } else {
      // Attack the player
      const damage = this.calculateDamage(this.enemy.attack, this.character.stats.constitution, randomMove.power);
      this.character.stats.hp = Math.max(0, this.character.stats.hp - damage);
      logMessage = `${this.enemy.name} uses ${randomMove.name} and deals ${damage} damage!`;
    }

    this.battleState.battleLog.push(logMessage);

    // Check if player is defeated
    if (this.character.stats.hp <= 0) {
      this.battleState.battleLog.push(`${this.character.name} is defeated!`);
      this.battleState.isActive = false;
      return this.battleState;
    }

    // Switch back to player turn
    this.battleState.currentTurn = 'player';
    this.battleState.turnCount++;

    return this.battleState;
  }

  // Calculate damage (Pokemon-style formula simplified)
  private calculateDamage(attackStat: number, defenseStat: number, movePower: number): number {
    const level = this.character.level;
    const random = 0.85 + Math.random() * 0.15; // Random factor between 0.85 and 1.0
    
    const damage = Math.floor(
      ((((2 * level + 10) / 250) * (attackStat / defenseStat) * movePower) + 2) * random
    );
    
    return Math.max(1, damage); // Minimum 1 damage
  }

  // Execute attack
  private executeAttack(attacker: Character, target: Enemy): string {
    const baseDamage = attacker.stats.strength + (attacker.mainClass?.hitDie || 6);
    const damage = this.calculateDamage(baseDamage, target.defense, 40); // Base move power of 40
    
    target.hp = Math.max(0, target.hp - damage);
    
    return `${attacker.name} attacks ${target.name} for ${damage} damage!`;
  }

  // Use item
  private useItem(itemId?: string): string {
    if (!itemId) return 'No item selected!';
    
    const itemIndex = this.character.inventory.findIndex(item => item.id === itemId);
    if (itemIndex === -1) return 'Item not found in inventory!';
    
    const item = this.character.inventory[itemIndex];
    
    if (item.type === 'consumable' && item.stats?.healing) {
      const healAmount = Math.min(item.stats.healing, this.character.stats.maxHp - this.character.stats.hp);
      this.character.stats.hp += healAmount;
      
      // Remove item from inventory
      this.character.inventory.splice(itemIndex, 1);
      
      return `${this.character.name} uses ${item.name} and recovers ${healAmount} HP!`;
    }
    
    return `${item.name} cannot be used in battle!`;
  }

  // Give rewards after victory
  private giveRewards(): void {
    if (!this.enemy) return;
    
    const expGained = this.enemy.level * 25;
    const goldGained = this.enemy.level * 10;
    
    this.character.experience += expGained;
    this.character.gold += goldGained;
    
    this.battleState.battleLog.push(`Gained ${expGained} experience and ${goldGained} gold!`);
    
    // Check for level up
    const expNeeded = this.character.level * 100;
    if (this.character.experience >= expNeeded) {
      this.character.level++;
      this.character.experience -= expNeeded;
      
      // Level up stat increases
      this.character.stats.maxHp += 10;
      this.character.stats.hp = this.character.stats.maxHp; // Full heal on level up
      this.character.stats.maxMp += 5;
      this.character.stats.mp = this.character.stats.maxMp;
      
      this.battleState.battleLog.push(`${this.character.name} reached level ${this.character.level}!`);
    }
  }

  // Get current battle state
  getBattleState(): BattleState {
    return this.battleState;
  }

  // End battle
  endBattle(): void {
    this.battleState.isActive = false;
    this.enemy = null;
    this.battleState.enemy = null;
    this.battleState.battleLog = [];
  }
}
