'use client';

import React from 'react';
import { useGame } from '@/context/GameContext';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';

const ClassSelector: React.FC = () => {
  const { 
    character, 
    availableClasses, 
    setMainClass, 
    setSecondaryClass, 
    canUnlockClass 
  } = useGame();

  if (!character) {
    return (
      <Card className="w-full">
        <CardHeader>
          <CardTitle>Class Selection</CardTitle>
          <CardDescription>Please select a race first to choose your classes</CardDescription>
        </CardHeader>
      </Card>
    );
  }

  const mainClasses = availableClasses.filter(c => c.type === 'main');
  const secondaryClasses = availableClasses.filter(c => c.type === 'secondary');

  const handleMainClassSelect = (gameClass: any) => {
    if (gameClass.unlocked) {
      setMainClass(gameClass);
    }
  };

  const handleSecondaryClassSelect = (gameClass: any) => {
    if (gameClass.unlocked) {
      setSecondaryClass(gameClass);
    }
  };

  const ClassCard = ({ 
    gameClass, 
    onSelect, 
    isSelected, 
    type 
  }: { 
    gameClass: any; 
    onSelect: (gameClass: any) => void; 
    isSelected: boolean;
    type: 'main' | 'secondary';
  }) => {
    const canUnlock = canUnlockClass(gameClass);
    const isLocked = !gameClass.unlocked && !canUnlock;

    return (
      <Card 
        className={`cursor-pointer transition-all ${
          isSelected 
            ? 'ring-2 ring-primary shadow-lg' 
            : isLocked 
              ? 'opacity-50 cursor-not-allowed' 
              : 'hover:shadow-md'
        }`}
      >
        <CardHeader>
          <CardTitle className="flex items-center justify-between">
            <span className="flex items-center gap-2">
              {gameClass.name}
              {isSelected && <Badge variant="default">Selected</Badge>}
            </span>
            <div className="flex gap-1">
              <Badge variant="outline" className="text-xs">
                d{gameClass.hitDie}
              </Badge>
              {!gameClass.unlocked && (
                <Badge variant="destructive" className="text-xs">
                  Locked
                </Badge>
              )}
            </div>
          </CardTitle>
          <CardDescription>{gameClass.description}</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
            <div>
              <p className="text-sm font-medium mb-1">Primary Abilities:</p>
              <div className="flex flex-wrap gap-1">
                {gameClass.primaryAbility.map((ability: string, index: number) => (
                  <Badge key={index} variant="secondary" className="text-xs">
                    {ability}
                  </Badge>
                ))}
              </div>
            </div>
            
            <div>
              <p className="text-sm font-medium mb-1">Class Skills:</p>
              <div className="flex flex-wrap gap-1">
                {gameClass.skills.slice(0, 4).map((skill: string, index: number) => (
                  <Badge key={index} variant="outline" className="text-xs">
                    {skill}
                  </Badge>
                ))}
                {gameClass.skills.length > 4 && (
                  <Badge variant="outline" className="text-xs">
                    +{gameClass.skills.length - 4} more
                  </Badge>
                )}
              </div>
            </div>
            
            {!gameClass.unlocked && (
              <div className="p-2 bg-muted rounded-md">
                <p className="text-xs text-muted-foreground">
                  <strong>Unlock Requirement:</strong> {gameClass.unlockRequirement}
                </p>
              </div>
            )}
            
            <Button 
              onClick={() => onSelect(gameClass)}
              disabled={isLocked}
              variant={isSelected ? "secondary" : "default"}
              className="w-full"
            >
              {isSelected 
                ? `Selected as ${type} class` 
                : isLocked 
                  ? 'Locked' 
                  : `Select as ${type} class`
              }
            </Button>
          </div>
        </CardContent>
      </Card>
    );
  };

  return (
    <div className="w-full">
      <div className="mb-6">
        <h2 className="text-2xl font-bold mb-2">Choose Your Classes</h2>
        <p className="text-muted-foreground">
          Select a main class and optionally a secondary class. Some classes must be unlocked through gameplay.
        </p>
      </div>

      <Tabs defaultValue="main" className="w-full">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="main">Main Classes</TabsTrigger>
          <TabsTrigger value="secondary">Secondary Classes</TabsTrigger>
        </TabsList>
        
        <TabsContent value="main" className="space-y-4">
          <div className="mb-4">
            <h3 className="text-lg font-semibold mb-2">Main Class</h3>
            <p className="text-sm text-muted-foreground">
              Your primary class defines your core abilities and role in combat.
            </p>
            {character.mainClass && (
              <div className="mt-2 p-2 bg-muted rounded-md">
                <p className="text-sm">
                  <strong>Current:</strong> {character.mainClass.name}
                </p>
              </div>
            )}
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {mainClasses.map((gameClass) => (
              <ClassCard
                key={gameClass.id}
                gameClass={gameClass}
                onSelect={handleMainClassSelect}
                isSelected={character.mainClass?.id === gameClass.id}
                type="main"
              />
            ))}
          </div>
        </TabsContent>
        
        <TabsContent value="secondary" className="space-y-4">
          <div className="mb-4">
            <h3 className="text-lg font-semibold mb-2">Secondary Class</h3>
            <p className="text-sm text-muted-foreground">
              A secondary class provides additional abilities and versatility. Most secondary classes must be unlocked.
            </p>
            {character.secondaryClass && (
              <div className="mt-2 p-2 bg-muted rounded-md">
                <p className="text-sm">
                  <strong>Current:</strong> {character.secondaryClass.name}
                </p>
              </div>
            )}
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {secondaryClasses.map((gameClass) => (
              <ClassCard
                key={gameClass.id}
                gameClass={gameClass}
                onSelect={handleSecondaryClassSelect}
                isSelected={character.secondaryClass?.id === gameClass.id}
                type="secondary"
              />
            ))}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
};

export default ClassSelector;
