// Game Data Models and Sample Data - D&D Inspired

export interface Race {
  id: string;
  name: string;
  description: string;
  bonuses: {
    strength: number;
    dexterity: number;
    constitution: number;
    intelligence: number;
    wisdom: number;
    charisma: number;
  };
  traits: string[];
}

export interface GameClass {
  id: string;
  name: string;
  description: string;
  type: 'main' | 'secondary';
  unlocked: boolean;
  unlockRequirement: string;
  hitDie: number;
  primaryAbility: string[];
  skills: string[];
}

export interface Item {
  id: string;
  name: string;
  type: 'weapon' | 'armor' | 'consumable' | 'misc';
  description: string;
  rarity: 'common' | 'uncommon' | 'rare' | 'epic' | 'legendary';
  stats?: {
    damage?: number;
    armor?: number;
    healing?: number;
  };
  value: number;
}

export interface Mission {
  id: string;
  name: string;
  description: string;
  location: string;
  difficulty: 'easy' | 'medium' | 'hard' | 'legendary';
  rewards: {
    experience: number;
    gold: number;
    items: string[];
    unlocksClass?: string;
  };
  completed: boolean;
}

export interface Character {
  id: string;
  name: string;
  level: number;
  experience: number;
  race: Race;
  mainClass: GameClass | null;
  secondaryClass: GameClass | null;
  stats: {
    hp: number;
    maxHp: number;
    mp: number;
    maxMp: number;
    strength: number;
    dexterity: number;
    constitution: number;
    intelligence: number;
    wisdom: number;
    charisma: number;
  };
  inventory: Item[];
  gold: number;
}

export interface BattleState {
  isActive: boolean;
  currentTurn: 'player' | 'enemy';
  turnCount: number;
  battleLog: string[];
  enemy: {
    name: string;
    hp: number;
    maxHp: number;
    level: number;
  } | null;
}

