'use client';

import React from 'react';
import { useGame } from '@/context/GameContext';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';

const RaceSelector: React.FC = () => {
  const { character, availableRaces, createCharacter } = useGame();

  const handleRaceSelect = (race: any) => {
    if (!character) {
      // If no character exists, create one with a default name
      const defaultName = `${race.name} Adventurer`;
      createCharacter(defaultName, race);
    }
  };

  if (character) {
    return (
      <Card className="w-full">
        <CardHeader>
          <CardTitle>Character Race</CardTitle>
          <CardDescription>Your selected race and bonuses</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-between">
            <div>
              <h3 className="text-lg font-semibold">{character.race.name}</h3>
              <p className="text-sm text-muted-foreground">{character.race.description}</p>
            </div>
            <div className="text-right">
              <p className="text-sm font-medium">Racial Bonuses:</p>
              <div className="flex flex-wrap gap-1 mt-1">
                {Object.entries(character.race.bonuses).map(([stat, bonus]) => (
                  bonus > 0 && (
                    <Badge key={stat} variant="secondary" className="text-xs">
                      {stat.charAt(0).toUpperCase() + stat.slice(1)} +{bonus}
                    </Badge>
                  )
                ))}
              </div>
            </div>
          </div>
          <div className="mt-4">
            <p className="text-sm font-medium mb-2">Racial Traits:</p>
            <div className="flex flex-wrap gap-1">
              {character.race.traits.map((trait, index) => (
                <Badge key={index} variant="outline" className="text-xs">
                  {trait}
                </Badge>
              ))}
            </div>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="w-full">
      <div className="mb-6">
        <h2 className="text-2xl font-bold mb-2">Choose Your Race</h2>
        <p className="text-muted-foreground">
          Select a race to begin your adventure. Each race provides unique bonuses and traits.
        </p>
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {availableRaces.map((race) => (
          <Card key={race.id} className="cursor-pointer hover:shadow-lg transition-shadow">
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                {race.name}
                <div className="flex gap-1">
                  {Object.entries(race.bonuses).map(([stat, bonus]) => (
                    bonus > 0 && (
                      <Badge key={stat} variant="secondary" className="text-xs">
                        +{bonus}
                      </Badge>
                    )
                  ))}
                </div>
              </CardTitle>
              <CardDescription>{race.description}</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                <div>
                  <p className="text-sm font-medium mb-2">Ability Bonuses:</p>
                  <div className="grid grid-cols-2 gap-1 text-xs">
                    {Object.entries(race.bonuses).map(([stat, bonus]) => (
                      <div key={stat} className="flex justify-between">
                        <span className="capitalize">{stat}:</span>
                        <span className={bonus > 0 ? 'text-green-600 font-medium' : 'text-muted-foreground'}>
                          {bonus > 0 ? `+${bonus}` : '0'}
                        </span>
                      </div>
                    ))}
                  </div>
                </div>
                
                <div>
                  <p className="text-sm font-medium mb-2">Racial Traits:</p>
                  <div className="flex flex-wrap gap-1">
                    {race.traits.map((trait, index) => (
                      <Badge key={index} variant="outline" className="text-xs">
                        {trait}
                      </Badge>
                    ))}
                  </div>
                </div>
                
                <Button 
                  onClick={() => handleRaceSelect(race)}
                  className="w-full mt-4"
                >
                  Select {race.name}
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  );
};

export default RaceSelector;
