'use client';

import React, { createContext, useContext, useState, useCallback } from 'react';
import { Race, GameClass, Item, Mission, Character, BattleState, RACES, CLASSES, SAMPLE_ITEMS, SAMPLE_MISSIONS } from '@/lib/gameData';
import { BattleEngine, BattleAction } from '@/lib/battleEngine';

interface GameContextType {
  // Character state
  character: Character | null;
  
  // Available data
  availableRaces: Race[];
  availableClasses: GameClass[];
  availableMissions: Mission[];
  
  // Battle state
  battleState: BattleState;
  battleEngine: BattleEngine | null;
  
  // Actions
  createCharacter: (name: string, race: Race) => void;
  setMainClass: (gameClass: GameClass) => void;
  setSecondaryClass: (gameClass: GameClass) => void;
  addItemToInventory: (item: Item) => void;
  createCustomItem: (itemData: Omit<Item, 'id'>) => void;
  createCustomMission: (missionData: Omit<Mission, 'id' | 'completed'>) => void;
  completeMission: (missionId: string) => void;
  relocateMission: (missionId: string, newLocation: string) => void;
  
  // Battle actions
  startBattle: () => void;
  executePlayerAction: (action: BattleAction) => void;
  executeEnemyTurn: () => void;
  endBattle: () => void;
  
  // Utility
  canUnlockClass: (gameClass: GameClass) => boolean;
  unlockClass: (classId: string) => void;
}

const GameContext = createContext<GameContextType | undefined>(undefined);

export const useGame = () => {
  const context = useContext(GameContext);
  if (!context) {
    throw new Error('useGame must be used within a GameProvider');
  }
  return context;
};

export const GameProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  // Initialize state
  const [character, setCharacter] = useState<Character | null>(null);
  const [availableRaces] = useState<Race[]>(RACES);
  const [availableClasses, setAvailableClasses] = useState<GameClass[]>(CLASSES);
  const [availableMissions, setAvailableMissions] = useState<Mission[]>(SAMPLE_MISSIONS);
  const [battleEngine, setBattleEngine] = useState<BattleEngine | null>(null);
  const [battleState, setBattleState] = useState<BattleState>({
    isActive: false,
    currentTurn: 'player',
    turnCount: 0,
    battleLog: [],
    enemy: null
  });

  // Create character
  const createCharacter = useCallback((name: string, race: Race) => {
    try {
      const newCharacter: Character = {
        id: `char_${Date.now()}`,
        name,
        level: 1,
        experience: 0,
        race,
        mainClass: null,
        secondaryClass: null,
        stats: {
          hp: 20 + race.bonuses.constitution * 2,
          maxHp: 20 + race.bonuses.constitution * 2,
          mp: 10 + race.bonuses.intelligence,
          maxMp: 10 + race.bonuses.intelligence,
          strength: 10 + race.bonuses.strength,
          dexterity: 10 + race.bonuses.dexterity,
          constitution: 10 + race.bonuses.constitution,
          intelligence: 10 + race.bonuses.intelligence,
          wisdom: 10 + race.bonuses.wisdom,
          charisma: 10 + race.bonuses.charisma,
        },
        inventory: [...SAMPLE_ITEMS],
        gold: 100
      };
      
      setCharacter(newCharacter);
      
      // Initialize battle engine
      const engine = new BattleEngine(newCharacter);
      setBattleEngine(engine);
    } catch (error) {
      console.error('Error creating character:', error);
    }
  }, []);

  // Set main class
  const setMainClass = useCallback((gameClass: GameClass) => {
    if (!character) return;
    
    try {
      if (!gameClass.unlocked) {
        console.warn('Cannot select locked class');
        return;
      }
      
      const updatedCharacter = {
        ...character,
        mainClass: gameClass,
        stats: {
          ...character.stats,
          maxHp: character.stats.maxHp + gameClass.hitDie,
          hp: character.stats.hp + gameClass.hitDie
        }
      };
      
      setCharacter(updatedCharacter);
      
      // Update battle engine
      if (battleEngine) {
        const newEngine = new BattleEngine(updatedCharacter);
        setBattleEngine(newEngine);
      }
    } catch (error) {
      console.error('Error setting main class:', error);
    }
  }, [character, battleEngine]);

  // Set secondary class
  const setSecondaryClass = useCallback((gameClass: GameClass) => {
    if (!character) return;
    
    try {
      if (!gameClass.unlocked) {
        console.warn('Cannot select locked class');
        return;
      }
      
      const updatedCharacter = {
        ...character,
        secondaryClass: gameClass,
        stats: {
          ...character.stats,
          maxHp: character.stats.maxHp + Math.floor(gameClass.hitDie / 2),
          hp: character.stats.hp + Math.floor(gameClass.hitDie / 2)
        }
      };
      
      setCharacter(updatedCharacter);
      
      // Update battle engine
      if (battleEngine) {
        const newEngine = new BattleEngine(updatedCharacter);
        setBattleEngine(newEngine);
      }
    } catch (error) {
      console.error('Error setting secondary class:', error);
    }
  }, [character, battleEngine]);

  // Add item to inventory
  const addItemToInventory = useCallback((item: Item) => {
    if (!character) return;
    
    try {
      setCharacter(prev => prev ? {
        ...prev,
        inventory: [...prev.inventory, item]
      } : null);
    } catch (error) {
      console.error('Error adding item to inventory:', error);
    }
  }, [character]);

  // Create custom item
  const createCustomItem = useCallback((itemData: Omit<Item, 'id'>) => {
    try {
      const newItem: Item = {
        ...itemData,
        id: `item_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
      };
      
      addItemToInventory(newItem);
    } catch (error) {
      console.error('Error creating custom item:', error);
    }
  }, [addItemToInventory]);

  // Create custom mission
  const createCustomMission = useCallback((missionData: Omit<Mission, 'id' | 'completed'>) => {
    try {
      const newMission: Mission = {
        ...missionData,
        id: `mission_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
        completed: false
      };
      
      setAvailableMissions(prev => [...prev, newMission]);
    } catch (error) {
      console.error('Error creating custom mission:', error);
    }
  }, []);

  // Complete mission
  const completeMission = useCallback((missionId: string) => {
    if (!character) return;
    
    try {
      const mission = availableMissions.find(m => m.id === missionId);
      if (!mission || mission.completed) return;
      
      // Mark mission as completed
      setAvailableMissions(prev => 
        prev.map(m => m.id === missionId ? { ...m, completed: true } : m)
      );
      
      // Give rewards
      const updatedCharacter = {
        ...character,
        experience: character.experience + mission.rewards.experience,
        gold: character.gold + mission.rewards.gold
      };
      
      setCharacter(updatedCharacter);
      
      // Unlock class if applicable
      if (mission.rewards.unlocksClass) {
        unlockClass(mission.rewards.unlocksClass);
      }
      
      // Add reward items
      mission.rewards.items.forEach(itemId => {
        const item = SAMPLE_ITEMS.find(i => i.id === itemId);
        if (item) {
          addItemToInventory(item);
        }
      });
    } catch (error) {
      console.error('Error completing mission:', error);
    }
  }, [character, availableMissions, addItemToInventory]);

  // Relocate mission
  const relocateMission = useCallback((missionId: string, newLocation: string) => {
    try {
      setAvailableMissions(prev =>
        prev.map(m => m.id === missionId ? { ...m, location: newLocation } : m)
      );
    } catch (error) {
      console.error('Error relocating mission:', error);
    }
  }, []);

  // Check if class can be unlocked
  const canUnlockClass = useCallback((gameClass: GameClass): boolean => {
    if (!character || gameClass.unlocked) return false;
    
    // Check level requirements
    if (gameClass.unlockRequirement.includes('level')) {
      const levelMatch = gameClass.unlockRequirement.match(/level (\d+)/);
      if (levelMatch) {
        const requiredLevel = parseInt(levelMatch[1]);
        if (character.level < requiredLevel) return false;
      }
    }
    
    // Check stat requirements
    if (gameClass.unlockRequirement.includes('Charisma 15+')) {
      if (character.stats.charisma < 15) return false;
    }
    
    // Check mission completion
    const missionRequirement = gameClass.unlockRequirement.match(/"([^"]+)"/);
    if (missionRequirement) {
      const missionName = missionRequirement[1];
      const mission = availableMissions.find(m => m.name === missionName);
      if (!mission || !mission.completed) return false;
    }
    
    return true;
  }, [character, availableMissions]);

  // Unlock class
  const unlockClass = useCallback((classId: string) => {
    try {
      setAvailableClasses(prev =>
        prev.map(c => c.id === classId ? { ...c, unlocked: true } : c)
      );
    } catch (error) {
      console.error('Error unlocking class:', error);
    }
  }, []);

  // Battle actions
  const startBattle = useCallback(() => {
    if (!battleEngine) return;
    
    try {
      const newBattleState = battleEngine.initializeBattle();
      setBattleState(newBattleState);
    } catch (error) {
      console.error('Error starting battle:', error);
    }
  }, [battleEngine]);

  const executePlayerAction = useCallback((action: BattleAction) => {
    if (!battleEngine) return;
    
    try {
      const newBattleState = battleEngine.executePlayerAction(action);
      setBattleState(newBattleState);
      
      // Update character state if it was modified
      if (character) {
        setCharacter({ ...character });
      }
    } catch (error) {
      console.error('Error executing player action:', error);
    }
  }, [battleEngine, character]);

  const executeEnemyTurn = useCallback(() => {
    if (!battleEngine) return;
    
    try {
      const newBattleState = battleEngine.executeEnemyTurn();
      setBattleState(newBattleState);
      
      // Update character state if it was modified
      if (character) {
        setCharacter({ ...character });
      }
    } catch (error) {
      console.error('Error executing enemy turn:', error);
    }
  }, [battleEngine, character]);

  const endBattle = useCallback(() => {
    if (!battleEngine) return;
    
    try {
      battleEngine.endBattle();
      setBattleState({
        isActive: false,
        currentTurn: 'player',
        turnCount: 0,
        battleLog: [],
        enemy: null
      });
    } catch (error) {
      console.error('Error ending battle:', error);
    }
  }, [battleEngine]);

  const value: GameContextType = {
    character,
    availableRaces,
    availableClasses,
    availableMissions,
    battleState,
    battleEngine,
    createCharacter,
    setMainClass,
    setSecondaryClass,
    addItemToInventory,
    createCustomItem,
    createCustomMission,
    completeMission,
    relocateMission,
    canUnlockClass,
    unlockClass,
    startBattle,
    executePlayerAction,
    executeEnemyTurn,
    endBattle
  };

  return (
    <GameContext.Provider value={value}>
      {children}
    </GameContext.Provider>
  );
};
